





















































































import {
    mapState,
    mapActions
} from 'vuex'
export default {
	data() {
		const initDate = this.getDate({
			format: true
		})
		return {
			id: '',
			orno: '',
			date: initDate,
			orderDetail: {}
		}
	},
	computed: {
		...mapState({
		  urlPre: state => state.urlPre,
		  userRole: state => state.userRole
		})
	},
	onLoad: function (option) {
		this.id = option.id
		this.orno = option.orno
		this.getDetail(option.id)
	},
	methods: {
		getDetail (ID) {
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url: this.urlPre + '/page/detail.do',
				method: 'GET',
				data: {
					id: ID
				},
				header: {
				  'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					switch (res.data.result) {
						case 1:
							this.date = (res.data.applylist[0].fgodate).slice(0, 10)
							this.orderDetail = res.data.applylist[0]
							break
						default:
							uni.showToast({
								image: '/static/icons/attention.png',
								title: '详情获取失败!'
							})
					}
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		bindDate (e) {
			this.date = e.target.value
		},
		modifyDate () {
			uni.showLoading({
				title: '加载中'
			})
			uni.request({
				url: this.urlPre + '/page/UpdateDate.do',
				method: 'GET',
				data: {
					id: this.id,
					fgodate: this.date
				},
				header: {
				  'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					switch (res.data.result) {
						case 1:
							uni.showToast({
								icon: 'success',
								title: '修改成功!'
							})
							this.getDetail(this.id)
							break
						default:
							uni.showToast({
								image: '/static/icons/attention.png',
								title: '修改失败!'
							})
					}
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		getDate(type) {
			const date = new Date()
			let year = date.getFullYear()
			let month = date.getMonth() + 1
			let day = date.getDate()
		
			if (type === 'start') {
				year = year - 60
			} else if (type === 'end') {
				year = year + 2
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day
			return `${year}-${month}-${day}`
		}
	}
}
